document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});

function iniciarApp() {
    try {
        sliderHero();
    } catch (err) {
        console.log('Hero');
    }
    try {
        crearGaleria();
    } catch (err) {
        console.log('NO EXISTE GALERIA');
    }
    cambioPopularesNuevos();
}

/* SLIDER HERO */

function sliderHero() {
    let counter = 1;
    setInterval(function() {
        document.getElementById('radio' + counter).checked = true;
        counter++;
        if(counter > 3) {
            counter = 1;
        }
    }, 5000);
}

/* CREACION DE GALERIA */

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');
    const urlActual = window.location.href;

    let nombreProducto;
    let numeroImagenes;

    if(urlActual.includes('osito_tuto')) {
        nombreProducto = 'Osito_Tuto';
        numeroImagenes = 3;
    } else if(urlActual.includes('rinoceronte_peluche')) {
        nombreProducto = 'Rinoceronte_peluche';
        numeroImagenes = 2;
    } else if(urlActual.includes('adornos_navidenos_pack')) {
        nombreProducto = 'Adornos_navideños_pack';
        numeroImagenes = 2;
    } else if(urlActual.includes('bolso_trapillo')) {
        nombreProducto = 'Bolso_Trapillo';
        numeroImagenes = 1;
    } else if(urlActual.includes('jirafa')) {
        nombreProducto = 'jirafa';
        numeroImagenes = 1;
    } else if(urlActual.includes('llavero_einstein')) {
        nombreProducto = 'llavero_einstein';
        numeroImagenes = 1;
    } else if(urlActual.includes('nenuco_de_apego')) {
        nombreProducto = 'Nenuco_de_apego';
        numeroImagenes = 1;
    } else if(urlActual.includes('oruga')) {
        nombreProducto = 'oruga';
        numeroImagenes = 3;
    } else if(urlActual.includes('pesebre_3_piezas')) {
        nombreProducto = 'Pesebre_3_piezas';
        numeroImagenes = 3;
    } else if(urlActual.includes('pesebre_4_piezas')) {
        nombreProducto = 'Pesebre_4_piezas';
        numeroImagenes = 3;
    } else if(urlActual.includes('unicornio')) {
        nombreProducto = 'unicornio';
        numeroImagenes = 3;
    }
    else {
        console.log('Error con la selección de imagenes de galeria');
    }

    /* REEMPLAZO IMAGEN PRINCIPAL */
    const imagen = document.createElement('picture');
    imagen.innerHTML = `
    <source srcset="build/img/${nombreProducto}1.webp" type="image/webp">
    <source srcset="build/img/${nombreProducto}1.jpg" type="image/jpeg">
    <img loading="lazy" src="build/img/${nombreProducto}1.jpg" alt="imagen galeria">
    `;

    const estaImagen = document.querySelector('.imagen-producto');
    const parent = estaImagen.parentNode;

    parent.replaceChild(imagen, estaImagen);

    imagen.setAttribute('class', 'imagen-producto');

    /* CREACION DE GALERIA */
    for(let i = 1; i <= numeroImagenes; i++) {

        const imagen = document.createElement('picture');
        imagen.innerHTML = `
        <source srcset="build/img/${nombreProducto}${i}.webp" type="image/webp">
        <source srcset="build/img/${nombreProducto}${i}.jpg" type="image/jpeg">
        <img loading="lazy" width="200" height="300" src="build/img/${nombreProducto}${i}.jpg" alt="imagen galeria">
        `;

        imagen.onclick = function() {
            //mostrarImagen(i);
            reemplazarImagen(i, nombreProducto);
        }

        galeria.appendChild(imagen);
    }
}

function reemplazarImagen(id, nombre) {
    const imagen = document.createElement('picture');
    imagen.innerHTML = `
    <source srcset="build/img/${nombre}${id}.webp" type="image/webp">
    <source srcset="build/img/${nombre}${id}.jpg" type="image/jpeg">
    <img loading="lazy" src="build/img/${nombre}${id}.jpg" alt="imagen galeria">
    `;

    const estaImagen = document.querySelector('.imagen-producto');
    const parent = estaImagen.parentNode;

    parent.replaceChild(imagen, estaImagen);

    imagen.setAttribute('class', 'imagen-producto');
}

/* CAMBIO DE POPULARES Y NUEVOS */

function cambioPopularesNuevos() {

    //POPULARES
    const imagenP = document.createElement('picture');
    imagenP.innerHTML = `
    <source srcset="build/img/oruga1.webp" type="image/webp">
    <source srcset="build/img/oruga1.jpg" type="image/jpeg">
    <img loading="lazy" src="build/img/oruga1.jpg" alt="imagen galeria">
    `;
    const enlaceP = document.querySelector('.populares .contenedor-imagen a');

    const estaImagenP = document.querySelector('.populares .contenedor-imagen a picture');
    const parentP = estaImagenP.parentNode;

    parentP.replaceChild(imagenP, estaImagenP);

    enlaceP.setAttribute('href', 'oruga.php');

    //NUEVOS
    const imagenN = document.createElement('picture');
    imagenN.innerHTML = `
    <source srcset="build/img/jirafa1.webp" type="image/webp">
    <source srcset="build/img/jirafa1.jpg" type="image/jpeg">
    <img loading="lazy" src="build/img/jirafa1.jpg" alt="imagen galeria">
    `;
    const enlaceN = document.querySelector('.nuevos .contenedor-imagen a');

    const estaImagenN = document.querySelector('.nuevos .contenedor-imagen a picture');
    const parentN = estaImagenN.parentNode;

    parentN.replaceChild(imagenN, estaImagenN);

    enlaceN.setAttribute('href', 'jirafa.php');
}



